@font-face {
  font-family: 'RobotoMono';
  src : url(/react-calculator/static/media/RobotoMono-Thin.b93c2ad4.ttf);
}

*{
  font-family: 'RobotoMono';
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  color:#fff;
  text-align: center;
  background: linear-gradient(to right, rgb(185, 185, 185), rgb(0, 67, 94) )
}
.calculator{
    width: 235px;
    height: 320px;
    border-radius: 5px;
    overflow: hidden;
    display: grid;
    grid-template-columns: repeat(4, 25%);
    grid-template-rows: 1fr 48px 48px 48px 48px 48px; 
}
:root {
    --bg-button: #f0f0f0;
    --border-button: solid 1px #888;
}

.button {
    font-size: 1em;
    background-color: #f0f0f0;
    background-color: var(--bg-button);
    border: none;
    border-right: solid 1px #888;
    border-right: var(--border-button);
    border-bottom: solid 1px #888;
    border-bottom: var(--border-button);
    outline: none;
}

.button:active {
    background-color: #ccc;
}

.button.doble {
    grid-column: span 2;
}

.button.triple {
    grid-column: span 3;
}

.button.operation {
    color: #fff;
    background-color: #fa8321;
}

.button.operation:active {
    background-color: #df6400;
}
.display{
  grid-column: span 4;
  background: #000;   
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 20px;
  overflow: hidden;
  font-size: 2em;
}
